import React, { createContext, useContext } from 'react';
import { useImmerReducer } from 'use-immer';
import { appDataReducer, IAction } from '../components/reducer/AppReducer';
import { IAppDataState } from './Interface';

const reqImages = require.context('../assets/homePage/homePageImages', true, /\.jpg$/);
const images = reqImages.keys().map((path) => reqImages(path));

