import React, { FC } from 'react';
import { IProgramInfo } from '../../../context/Interface';
import '../../../styles/home/ProgramCard.scss';
type Props = {
	programInfo: IProgramInfo;
	index: number;
};
const ProgramCard: FC<Props> = (props: Props) => {
	const { programInfo, index } = props;

	return (
		<div className='card-container'>
			<div className='card-wrapper'>
				<div className='index'>{index > 9 ? index : `0${index}`}</div>
				<div className='title'>{programInfo.title}</div>
				<div className='info'>{programInfo.info}</div>
			</div>
		</div>
	);
};

export default ProgramCard;
