import React, { FC, useEffect, useRef } from 'react';
import leftArrow from '../../../assets/homePage/leftArrow.svg';
import rightArrow from '../../../assets/homePage/rightArrow.svg';
import '../../../styles/home/ImageSlider.scss';
const reqImages = require.context('../../../assets/homePage/homePageImages', true, /\.png$/);
const images = reqImages.keys().map((path) => reqImages(path));
const ImageSlider: FC = () => {
	let counter = 1;
	const slider = useRef<HTMLDivElement>(null);
	const dot = useRef<HTMLDivElement[]>([]);
	useEffect(() => {
		if (dot.current[0]) {
			dot.current[0].style.opacity = '1';
		}
	}, []);
	const toLeft = () => {
		if (slider.current) {
			if (counter > 0) {
				dot.current[counter - 1].style.opacity = '0.2';
				counter--;
				slider.current.style.transition = 'transform 0.4s ease-in-out';
				slider.current.style.transform = 'translateX(' + -counter + '00%)';
				if (counter === 0) {
					dot.current[images.length - 1].style.opacity = '100%';
				} else {
					dot.current[counter - 1].style.opacity = '1';
				}
			}
		}
	};
	const toRight = () => {
		if (slider.current) {
			if (counter < images.length + 1) {
				dot.current[counter - 1].style.opacity = '0.2';
				counter++;
				slider.current.style.transition = 'transform 0.4s ease-in-out';
				slider.current.style.transform = 'translateX(' + -counter + '00%)';
				if (counter === images.length + 1) {
					dot.current[0].style.opacity = '1';
				} else {
					dot.current[counter - 1].style.opacity = '1';
				}
			}
		}
	};
	const check = () => {
		console.log(counter);
		if (slider.current) {
			if (counter === images.length + 1) {
				slider.current.style.transition = 'none';
				counter = 1;
				slider.current.style.transform = 'translateX(' + -counter + '00%)';
			}
			if (counter === 0) {
				slider.current.style.transition = 'none';
				counter = images.length;
				slider.current.style.transform = 'translateX(' + -counter + '00%)';
			}
		}
	};
	return (
		<div className='pic-container'>
			<div className='pic-wrapper'>
				<img src={leftArrow} alt='leftArrow' id='leftArrow' onClick={toLeft} />
				<img src={rightArrow} alt='rightArrow' id='rightArrow' onClick={toRight} />
				<div className='slider' ref={slider} onTransitionEnd={check}>
					<img src={images[images.length - 1]} alt='pic' id='last-pic' />
					{images.map((url, index) => (
						<img src={url} alt={'pic' + index} />
					))}
					<img src={images[0]} alt={'pic'} id='first-pic' />
				</div>
			</div>
			<div className='dot-wrapper'>
				{images.map((url, index) => (
					<div className='pic-dot' ref={(el) => (dot.current[index] = el!)} />
				))}
			</div>
		</div>
	);
};

export default ImageSlider;
