import { IAppDataState } from '../../context/Interface';

export type IAction = { type: ActionType.SetImgURLs; payload: string[] };
export enum ActionType {
	SetImgURLs
}
export const appDataReducer = (draft: IAppDataState, action: IAction) => {
	switch (action.type) {
		case ActionType.SetImgURLs:
			draft.imgURL = action.payload;
			return;
		default:
			return;
	}
};
