import React, { FC, useEffect, useRef } from 'react';
import { NavLink } from 'react-router-dom';
import logo from '../assets/logo.svg';
import '../styles/NavBar.scss';

export type TNav = { title: string; link: string };
const navBar: TNav[] = [
    { title: 'Home', link: '/' },
    { title: 'Programs', link: '/programs' },
    { title: 'Registration', link: '/registration' },
    { title: 'Achievements', link: '/achievements' },
    { title: 'Gallery', link: '/gallery' },
    { title: 'About us', link: '/about' }
];
const NavBar: FC = () => {
    const nav = useRef<HTMLElement>(null);

    useEffect(() => {
        window.onscroll = () => {
            if (nav.current) {
                if (window.pageYOffset > 0) {
                    nav.current.className = 'nav-container nav-scroll';
                } else {
                    nav.current.className = 'nav-container';
                }
            }
        };
    }, []);
    return (
        <nav className='nav-container' ref={nav}>
            <div className='nav-wrapper'>
                <div className='nav-symbol'>
                    <img className='nav-logo' src={logo} alt='WebLogo' />
                    <div className='nav-title'>Super Star Educational Group Inc.</div>
                </div>
                <input type='checkbox' name='' id='check' />
                <div className='nav-bar'>
                    {navBar.map((item) => (
                        <NavLink
                            to={item.link}
                            exact
                            className='nav-link'
                            activeClassName='nav-link-active'>
                            {item.title}
                        </NavLink>
                    ))}
                </div>
                <div className='hamburger-menu-wrapper'>
                    <div className='hamburger-menu'>
                        <div></div>
                    </div>
                </div>
                <div className='menu-items'>
                    {navBar.map((item) => (
                        <NavLink
                            to={item.link}
                            exact
                            className='menu-link'
                            activeClassName='menu-link-active'>
                            {item.title}
                        </NavLink>
                    ))}
                </div>
            </div>
        </nav>
    );
};

export default NavBar;
